import React, { useState } from "react";
import axios from "axios";
import { Container, Col, Form, Button, Alert, Row } from "react-bootstrap";

const Query = ({ isLogged }) => {
  const [serial, setSerial] = useState("");
  const [receivedCert, setReceivedCert] = useState({});
  const [alert, setAlert] = useState(false);

  const getCert = async (event) => {
    event.preventDefault();
    const URL = "http://localhost:5000/query/";

    try {
      const response = await axios.get(URL, { params: { serial } });

      if (response.data) {
        setReceivedCert(response.data);
      } else {
        setAlert(true);
      }
      setSerial("");
    } catch (error) {
      console.error(error);
    }
  };

  const deleteCert = async () => {
    try {
      await axios.delete("http://localhost:5000/admin/delete", {
        data: { serial: receivedCert.serial },
      });
      setReceivedCert({});
    } catch (error) {
      console.error(error);
    }
  };
  return (
    <Container className="border my-3 bg-light">
      <Alert
        show={alert}
        variant="danger"
        onClose={() => setAlert(false)}
        dismissible
      >
        <Alert.Heading>
          No certificate with this serial, please try again
        </Alert.Heading>
      </Alert>
      <Form className="p-3 mx-auto" onSubmit={getCert}>
        <Form.Label className="center">
          Find Certificate By Serial Number{" "}
        </Form.Label>

        <Form.Row className="align-items-center">
          <Col>
            <Form.Control
              type="text"
              className="mb-2"
              id="inlineFormInput"
              placeholder="Enter serial here..."
              value={serial}
              onChange={(event) => setSerial(event.target.value)}
            />
          </Col>
          <Col>
            <Button variant="success" type="submit" className="mb-2">
              Find Certificate
            </Button>
          </Col>
        </Form.Row>
      </Form>
      <div style={{ minHeight: `70vh` }} className="p-3">
        {isLogged && (
          <>
            <Button variant="danger" className="mb-2" onClick={deleteCert}>
              Kalıcı Olarak Sil
            </Button>
            <Row>
              <div className="col-md-6">
                <h5>
                  <strong>Serial Number:</strong>{" "}
                  <span style={{ color: "red", fontWeight: "bold" }}>
                    {" "}
                    &nbsp;{receivedCert.serial}
                  </span>
                </h5>
                <p>
                  <strong>Database Id:</strong> &nbsp;{receivedCert._id}
                </p>
              </div>
              <div className="col-md-6">
                <h6>
                  <strong>Info: </strong>&nbsp;{receivedCert.info}
                </h6>
                <p>
                  <strong>Created At:</strong> &nbsp;{receivedCert.date}
                </p>
              </div>
            </Row>
          </>
        )}
        <img src={receivedCert.image} className="img-fluid" alt="" />
      </div>
    </Container>
  );
};

export default Query;
