import React, { useState } from "react";
import { Carousel } from "react-bootstrap";
import "../style.css";

const Home = () => {
  const [index, setIndex] = useState(0);
  const handleSelect = (selectedIndex, e) => {
    setIndex(selectedIndex);
  };
  return (
    <>
      <Carousel
        activeIndex={index}
        onSelect={handleSelect}
        className="mx-auto"
        fade
      >
        <Carousel.Item interval={750}>
          <img className="d-block w-100" src="slide1.jpg" alt="First slide" />
          <Carousel.Caption>
            <h3 className="carousel-text">Consulting services</h3>
            <p className="carousel-text">
              MTLCERT provides value added research and consulting services to
              international as well as domestic companies and investors.
            </p>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item interval={750}>
          <img className="d-block w-100" src="slide2.jpg" alt="Second slide" />

          <Carousel.Caption>
            <h3 className="carousel-text">
              Accreditations And Certifications{" "}
            </h3>
            <p className="carousel-text">
              We are a fully accredited NVLAP ISO/IEC 17025 third-party test
              laboratory providing EMC, Environmental, Safety and Transportation
              testing.
            </p>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item interval={750}>
          <img className="d-block w-100" src="slide3.jpg" alt="Third slide" />

          <Carousel.Caption>
            <h3 className="carousel-text">Sistem Belgelendirme</h3>
            <p className="carousel-text">
              MTL olarak ISO 9001 veya ISO/TS 16949 standartları kapsamında
              denetim ve belgelendirme hizmetleri sunmaktayız.
            </p>
          </Carousel.Caption>
        </Carousel.Item>
      </Carousel>
      <br />
      <br />
      <h4 className="text-center">Certification</h4>
      <p className="px-5">
        From Field to Fashion: The GOTS quality assurance system is based on
        on-site inspection and certification of the entire textile supply chain
        (processing and trade). Operators from post-harvest handling up to
        garment making, as well as wholesalers (including exporters and
        importers), have to undergo an on-site annual inspection cycle and must
        hold a valid certification in order for the final products to be
        labelled as GOTS certified.
      </p>
      <p className="px-5">
        Any claim to be compliant to GOTS as the worldwide leading textile
        processing standard is invalid until an independent Certification Body
        has checked – through a set of inspection methods – and attested –
        through formally issued certification documents – that a product and its
        processor, manufacturer or trader is, indeed, GOTS compliant. There are
        two types of such certification documents: Scope Certificates (SCs)
        which proove that a supplier meets all criteria to be allowed to process
        GOTS goods and Transaction Certificates (TCs) which proove that the
        goods themselves meet all GOTS product criteria.
      </p>
    </>
  );
};

export default Home;
