import React, { useState } from "react";
import { Modal, Form, Button } from "react-bootstrap";
import "../style.css";
import axios from "axios";

const initial = { username: "", password: "" };
const Login = ({ isLogged, checkIsLogged, setIsSerkan }) => {
  const [loginModal, setLoginModal] = useState(false);
  const [loginInput, setLoginInput] = useState(initial);

  const login = async (event) => {
    event.preventDefault();
    const URL = "http://localhost:5000/auth/login";

    try {
      const response = await axios.post(URL, loginInput);
      await checkIsLogged();
      setLoginModal(false);
      if (response.data._id === "607cddb8b23ff90bf0c30d50") {
        setIsSerkan(true);
      }
    } catch (error) {
      console.error(error);
    }
  };

  const logout = async () => {
    const URL = "http://localhost:5000/auth/logout";
    await axios.get(URL);
    await checkIsLogged();
    setLoginInput(initial);
  };

  return (
    <>
      <i className="fas fa-search mr-3 ml-auto find">
        <span>&nbsp;Find</span>
      </i>
      {!isLogged ? (
        <div>
          <i
            className="fas fa-user-cog login"
            onClick={() => setLoginModal(true)}
          >
            <span>&nbsp;Login</span>
          </i>
        </div>
      ) : (
        <div>
          <i className="fas fa-user-cog auth" onClick={logout}>
            {" "}
            <span style={{ color: `#28a745` }}>
              Click to Logout:&nbsp;
              <strong>{isLogged && loginInput.username}</strong>&nbsp;
            </span>
          </i>
        </div>
      )}

      <Modal
        show={loginModal}
        onHide={() => setLoginModal(false)}
        backdrop="static"
        keyboard={false}
      >
        <Modal.Header closeButton>
          <Modal.Title>Admin Login</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form onSubmit={login}>
            <Form.Group controlId="formBasicEmail">
              <Form.Label>Username</Form.Label>
              <Form.Control
                type="text"
                placeholder="Enter username"
                value={loginInput.username}
                onChange={(event) =>
                  setLoginInput({
                    ...loginInput,
                    username: event.target.value,
                  })
                }
              />
            </Form.Group>

            <Form.Group controlId="formBasicPassword">
              <Form.Label>Password</Form.Label>
              <Form.Control
                type="password"
                placeholder="Password"
                value={loginInput.password}
                onChange={(event) =>
                  setLoginInput({
                    ...loginInput,
                    password: event.target.value,
                  })
                }
              />
            </Form.Group>

            <Button variant="success" className="me-auto" type="submit">
              Login
            </Button>
          </Form>
        </Modal.Body>
      </Modal>
    </>
  );
};

export default Login;
