import React, { useState, useEffect } from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import axios from "axios";
import Home from "./components/pages/Home";
import About from "./components/pages/About";
import Contact from "./components/pages/Contact";
import NavBar from "./components/NavBar";
import Services from "./components/pages/Services";
import Laboratory from "./components/pages/Laboratory";
import Footer from "./components/Footer";
import Query from "./components/pages/Query";
import AdminPanel from "./components/pages/AdminPanel";

axios.defaults.withCredentials = true;

const App = () => {
  const [isLogged, setIsLogged] = useState(undefined);
  const [isSerkan, setIsSerkan] = useState(false);

  const checkIsLogged = async () => {
    const URL = "http://localhost:5000/auth/loggedin";
    const loggedin_response = await axios.get(URL); //axios will get a True or False response
    setIsLogged(loggedin_response.data);
  };

  useEffect(() => {
    checkIsLogged();
  }, []);

  return (
    <Router>
      <div>
        <NavBar
          isLogged={isLogged}
          checkIsLogged={checkIsLogged}
          setIsSerkan={setIsSerkan}
        />
        <Switch>
          <Route exact path="/">
            <Home />
          </Route>
          <Route path="/about">
            <About />
          </Route>
          <Route path="/services">
            <Services />
          </Route>
          <Route path="/laboratory">
            <Laboratory />
          </Route>
          <Route path="/contact">
            <Contact />
          </Route>
          <Route path="/query">
            <Query isLogged={isLogged} />
          </Route>
          <Route path="/admin">
            <AdminPanel
              isLogged={isLogged}
              checkIsLogged={checkIsLogged}
              isSerkan={isSerkan}
            />
          </Route>
        </Switch>
        <Footer />
      </div>
    </Router>
  );
};

export default App;
