import React, { useState } from "react";
import axios from "axios";
import FileBase from "react-file-base64";
import { Container, Row, Card, Form, Button, Col } from "react-bootstrap";
import Register from "./Register";
const initial = { serial: "", image: "", info: "" };

const AdminPanel = ({ isLogged, checkIsLogged, isSerkan }) => {
  const [newCert, setNewCert] = useState(initial);

  const saveCert = async (event) => {
    event.preventDefault();
    const URL = "http://localhost:5000/admin";

    try {
      await axios.post(URL, newCert);
      setNewCert(initial);
    } catch (error) {
      console.error(error);
    }
  };
  return (
    <Container fluid>
      <Row className="justify-content-md-center m-3 border">
        <Col>
          <Form className="p-3" onSubmit={saveCert}>
            <Form.Group controlId="formBasicEmail">
              <Form.Label>Sertifika Numarası</Form.Label>
              <Form.Control
                type="text"
                placeholder="Enter serial here.."
                value={newCert.serial}
                onChange={(e) =>
                  setNewCert({ ...newCert, serial: e.target.value })
                }
              />
            </Form.Group>

            <Form.Group controlId="formBasicPassword">
              <Form.Label>Ekstra Bilgiler</Form.Label>
              <Form.Control
                type="text"
                placeholder="Enter here..."
                value={newCert.info}
                onChange={(e) =>
                  setNewCert({ ...newCert, info: e.target.value })
                }
              />
            </Form.Group>
            <Form.Group>
              <p>
                Bir resim yükleyin max. 250kb (Ceasium Image Compressor ile
                boyutunu küçültün)
              </p>
              <FileBase
                type="file"
                id="login"
                onDone={({ base64 }) =>
                  setNewCert({ ...newCert, image: base64 })
                }
              />
            </Form.Group>
            <Button variant="primary" type="submit" className="px-3">
              Kaydet
            </Button>
          </Form>{" "}
          {isSerkan && (
            <Register isLogged={isLogged} checkIsLogged={checkIsLogged} />
          )}
        </Col>
        <Col>
          <Card className="border-0 ">
            <Card.Img variant="bottom" src={newCert.image} />
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

export default AdminPanel;
