import React from "react";
import { Navbar, Nav } from "react-bootstrap";
import { Link } from "react-router-dom";
import Login from "./pages/Login";
import "./style.css";

const NavBar = ({ isLogged, checkIsLogged, setIsSerkan }) => {
  return (
    <>
      <div className="row contain">
        <img src="logd.png" alt="logo" className="col-6" />
        <div className="col-6 d-flex flex-column justify-content-around align-items-end p-2 pr-4">
          <i className="fas fa-map-marker-alt address">
            <span>&nbsp; Cihangir Mah. Atatürk Cad. 15/3 Avcılar/İstanbul</span>
          </i>{" "}
          <br />
          <i className="fas fa-phone-volume address">
            <span>&nbsp;+90 (212) 292 02 32 </span>
          </i>
          <br />
          <i className="fas fa-envelope address">
            <span>&nbsp;mtlcert@gmail.com</span>
          </i>
        </div>
      </div>
      <Navbar bg="light" expand="lg" collapseOnSelect>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="mr-auto">
            <Nav.Link eventKey="1" as={Link} to="/">
              Home
            </Nav.Link>
            <Nav.Link eventKey="2" as={Link} to="/about">
              About MTL
            </Nav.Link>
            <Nav.Link eventKey="3" as={Link} to="/services">
              Services
            </Nav.Link>
            <Nav.Link eventKey="4" as={Link} to="/laboratory">
              Laboratory
            </Nav.Link>
            <Nav.Link eventKey="6" as={Link} to="/query">
              Query
            </Nav.Link>
            {isLogged && (
              <Nav.Link eventKey="7" as={Link} to="/admin">
                Admin Panel
              </Nav.Link>
            )}
            <Nav.Link eventKey="5" as={Link} to="/contact">
              Contact
            </Nav.Link>
          </Nav>
        </Navbar.Collapse>
        <Login
          checkIsLogged={checkIsLogged}
          isLogged={isLogged}
          setIsSerkan={setIsSerkan}
        />
      </Navbar>
    </>
  );
};

export default NavBar;
